+component(switch)

  position: relative
  display: inline-block
  width: 60px
  height: 34px

  input
    display: none

    &:checked + .switch__slider
      background-color: $color-green
    &:focus + .switch__slider
      box-shadow: 0 0 1px $color-green
    &:checked + .switch__slider:before
      -webkit-transform: translateX(26px)
      -ms-transform: translateX(26px)
      transform: translateX(26px)

  +part(slider)
    position: absolute
    cursor: pointer
    top: 0
    left: 0
    right: 0
    bottom: 0
    background-color: #ccc
    -webkit-transition: .4s
    transition: .4s

    &:before
      position: absolute
      content: ""
      height: 26px
      width: 26px
      left: 4px
      bottom: 4px
      background-color: white
      -webkit-transition: .4s
      transition: .4s
    border-radius: 34px
    &:before
      border-radius: 50%
